export default class Auth {
    /**
     * 
     */
    static isLogin(): boolean {
        return localStorage.getItem('uid') !== null;
    }
    /**
     * 
     * @param uid UID
     */
    static login(uid: number): void {
        localStorage.setItem('uid', uid.toString());
    }
    /**
     * 
     */
    static logout(): void {
        localStorage.removeItem('uid');
    }
}


