const initialError = new Error('');
initialError.name = 'initial_error';
export default {
    user: {
        isLogin: false,
        userName: '',
        loading: false,
    },
    dashboard: {
        errors: {
            lastError: initialError,
            errorId: Math.random()
        },
        overview: {
            isLoading: false
        },
        realtime: {
            isLoading: false,
            recentEvents: [],
            recentJobs: []
        },
        server: {
            node: {
                isLoading: false,
                serverList: []
            }
        }
    },
};


